.tailwind-reset {
    all: initial;
    border-style: none;
}

@keyframes stardust {
    0%,
    100% {
        opacity: 0;
        transform: scale(0.5);
    }
    50% {
        opacity: 1;
        transform: scale(1);
    }
}

@keyframes natureFadeIn {
    0% {
        opacity: 0;
        transform: scale(0.95);
    }
    100% {
        opacity: 1;
        transform: scale(1);
    }
}

.fill-background {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    object-fit: cover;
    object-position: center;
}

.image-container {
    position: relative;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}

.container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    position: relative;
    overflow: hidden;
}

.logo-container {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: opacity 1s ease-in-out;
    opacity: 0;
}

.logo {
    max-width: 80%;
    max-height: 80%;
    border-radius: 45px;
    transition: opacity 1s ease-in-out;
}

.logo-text {
    margin-top: 10px;
    font-size: 24px;
    color: #0b444f;
    font-weight: bold;
    text-align: center;
}

.stardust {
    position: absolute;
    top: 65%;
    left: 65%;
    transform: translate(-50%, -50%);
    pointer-events: none;
}

.stardust img {
    width: 100px;
    height: 100px;
    animation: stardust 2s infinite;
}

.nature-fade-in {
    animation: natureFadeIn 2s forwards;
    opacity: 1;
}

.background-svg {
    /* position: absolute;
    top: 0;
    left: 0; */
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}

.background-svg img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.myname {
    width: 100%;
    height: 100%;
    object-fit: cover; /* Ensures the SVG covers the area while maintaining aspect ratio */
}

.container.fade-in .logo-container {
    opacity: 1;
    transition: opacity 2s ease-in-out;
}

.welcome-text {
    opacity: 0;
    animation: fadeIn 2s forwards; /* Fades in the welcome text */
    animation-delay: 1s; /* Delay to start after the nature background */
    position: absolute;
    top: 25%;
    left: 15%;
    width: 80%;
}

@keyframes fadeIn {
    to {
        opacity: 1;
    }
}

.swipe-up-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.emoji-text {
    font-size: 86px;
}
.swipe-up-text {
    font-family: "Arial", sans-serif;
    font-size: 38px;
    color: white;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
    text-align: center;
}

.swipe-up-animation {
    opacity: 0;
    animation:
        fadeInOut 3s infinite,
        floatUpDown 3s infinite;
    /* margin-top: 20px; */
    position: absolute;
    top: 70%;
    left: 0%;
    width: 100%;
}

@keyframes fadeInOut {
    0%,
    100% {
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
}

@keyframes floatUpDown {
    0%,
    100% {
        transform: translateY(0);
    }
    50% {
        transform: translateY(-8vh);
    }
}
.start-container {
    height: 200vh; /* Make the container taller to allow scrolling */
    position: relative;
    overflow: hidden;
}

.sun {
    position: fixed;
    top: 0;
    left: 0;
    transition: transform 0.3s ease-out;
}

